@import url("https://fonts.googleapis.com/css2?family=Press+Start+2P&family=Share+Tech+Mono&display=swap");
@import "variables";

body {
  margin: 0;
}
.app {
  font-family: "Share Tech Mono", monospace;
  font-size: $default-font-size;
  padding: 80px;

  h1,
  h2 {
    font-family: "Press Start 2P", monospace;
  }
  h1 {
    font-size: 2.5rem;
    line-height: 3.5rem;
  }
  h2 {
    font-size: $heading-font-size;
    line-height: 2.5rem;
  }

  .header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 40px;
    text-align: center;

    .logo {
      width: 160px;
      margin-bottom: 10px;
    }
  }
  .loading {
    color: $blue;
    width: fit-content;
    margin: auto;
  }
  .password {
    text-align: center;
    input {
      margin-right: 10px;
    }
  }
}

@media screen and (max-width: $breakpoint-md) {
  .app {
    padding: 40px;
    h2 {
      text-align: center;
    }
  }
}
@media screen and (max-width: $breakpoint-sm) {
  .app {
    padding: 20px;
    font-size: 1rem;

    h1 {
      font-size: 2rem;
      line-height: 3rem;
    }
    h2 {
      font-size: 1rem;
      line-height: 2rem;
    }
    .header .logo {
      width: 130px;
    }
  }
}
